@import url(https://fonts.googleapis.com/css2?family=Saira+Condensed:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=MuseoModerno:wght@900&family=Saira+Condensed:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=MuseoModerno:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=MuseoModerno:wght@600&family=Play&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}



* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.logo {
  font-family: "MuseoModerno", cursive;
  font-size: 18px;
  display: flex;
  justify-content: center;
  height: 3rem;
  text-align: center;
  padding-top: 1rem;
}

.navigation {
  font-family: "Play", sans-serif;
  display: flex;
  justify-content: space-around;
  padding-top: 1rem;
}

.navigation a {
  text-decoration: none;
  color: black;
}

.navigation a.active {
  border-bottom: 1px solid black;
}

.sub-nav {
  margin-top: 1rem;
  position: absolute;
  display: flex;
  z-index: -1;
}

.sub-nav li {
  list-style: none;
}

.sub-nav a {
  margin: 1.5rem 1rem;
}

.home-link:checked .sub-nav {
  z-index: 100;
}

button:focus {
  outline: none;
}

/*Home page button*/

.heading button {
  font-family: "Play", sans-serif;
  font-weight: bold;
  margin-top: 1.5rem;
  width: 6.8rem;
  height: 2.3rem;
  background-color: white;
  border: 0.1rem solid black;
  border-radius: 0.5rem;
  box-shadow: 3px 3px 10px black;
  cursor: pointer;
  margin-right: 2rem;
  font-size: 1.2rem;
}

.heading button:hover {
  box-shadow: 0px 0px 0px black;
  transition: 0.3s;
  box-shadow: inset 0 0 5px #000000;
}

/*Login page button*/
.login-button {
  font-family: "Play", sans-serif;
  font-weight: bold;
  margin-top: 1.5rem;
  width: 13rem;
  height: 2.3rem;
  background-color: white;
  border: 0.1rem solid rgb(34, 33, 33);
  border-radius: 0.5rem;
  box-shadow: 3px 3px 10px rgb(34, 33, 33);
  cursor: pointer;
  font-size: 1.2rem;
}

.login-button:hover {
  box-shadow: 0px 0px 0px rgb(34, 33, 33);
  transition: 0.2s;
}

.signup-button {
  font-family: "Play", sans-serif;
  font-weight: bold;
  margin-top: 1.5rem;
  width: 13rem;
  height: 2.3rem;
  background-color: rgb(34, 33, 33);
  border: 0.1rem solid white;
  border-radius: 0.5rem;
  box-shadow: 3px 3px 10px rgb(34, 33, 33);
  cursor: pointer;
  font-size: 1.2rem;
  color: white;
}

.signup-button:hover {
  box-shadow: 0px 0px 0px rgb(34, 33, 33);
  transition: 0.2s;
}

.cart-item-button {
  font-family: "Play", sans-serif;
  font-weight: bold;
  margin-top: 1.5rem;
  width: 13rem;
  height: 2.3rem;
  background-color: rgb(34, 33, 33);
  border: 0.1rem solid white;
  border-radius: 0.5rem;
  box-shadow: 3px 3px 10px rgb(34, 33, 33);
  cursor: pointer;
  font-size: 1.2rem;
  color: white;
}

.cart-item-button:hover {
  box-shadow: 0px 0px 0px rgb(34, 33, 33);
  transition: 0.2s;
}

button:disabled {
  background: white;
  box-shadow: none;
  color: gray;
  border: solid gray;
  cursor: default;
}

input {
  font-family: "Play", sans-serif;
  border: 0.1rem solid black;
  height: 2.3rem;
  width: 13rem;
  padding: 0.6rem;
  margin: 0.5rem;
  border-radius: 0.5rem;
}

input:focus {
  outline: none;
}

input::-webkit-input-placeholder {
  font-family: "Play", sans-serif;
  color: rgb(173, 172, 172);
}

input:-ms-input-placeholder {
  font-family: "Play", sans-serif;
  color: rgb(173, 172, 172);
}

input::-ms-input-placeholder {
  font-family: "Play", sans-serif;
  color: rgb(173, 172, 172);
}

input::placeholder {
  font-family: "Play", sans-serif;
  color: rgb(173, 172, 172);
}

.reg-input:focus {
  box-shadow: 0px 0px 10px rgb(95, 95, 95);
}

.footer-wrapper {
  width: 100%;
  display: flex;
  margin-top: 0.7rem;
  position: fixed;
  bottom: 0;
  background-color: white;
  padding: 0.7rem;
}

.footer-text {
  font-family: "Play", sans-serif;
  display: flex;
  flex-direction: row;
  justify-content: start;
  margin-left: 3rem;
  width: 50%;
}

.footer-wrapper a {
  color: black;
  text-decoration: none;
  padding: 0 1rem;
}

.footer-social {
  width: 50%;
  display: flex;
  justify-content: flex-end;
  margin-right: 3rem;
}

.svg-inline--fa.fa-w-16,
.svg-inline--fa.fa-w-14 {
  height: 1.5rem;
  width: 1.5rem;
}

@media only screen and (max-width: 767px) {
  .footer-text {
    margin: 0;
  }

  .footer-social {
    margin: 0;
  }

  .svg-inline--fa.fa-w-16,
  .svg-inline--fa.fa-w-14 {
    height: 1rem;
    width: 1rem;
  }

  .footer-wrapper {
    align-items: center;
  }
}

@media only screen and (max-width: 767px) {
  .footer-text {
    font-size: 0.7rem;
  }
}

@media only screen and (max-width: 330px) {
  .footer-text a {
    padding: 0.5rem;
  }
}

.cart {
  display: flex;
  flex-direction: column;
  padding: 4rem 2rem;
  text-align: center;
}

.cart-container {
  margin: 3rem auto 5rem auto;
  padding: 4rem;
  background-color: white;
  border-radius: 1.3rem;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 98%;
  box-shadow: 3px 3px 20px rgb(34, 33, 33);
}

.cart-items {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media only screen and (max-width: 684px) {
  .cart-items {
    display: block;
  }
}

@media only screen and (max-width: 330px) {
  .cart {
    padding: 1rem;
  }

  .cart-container {
    padding: 1rem;
  }
}

.cart-item img {
  display: flex;
  max-height: 90%;
  width: auto;
}

.cart-item-container {
  display: flex;
  flex-direction: row;
  width: 30%;
  height: 30%;
  justify-content: center;
}

.cart-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem;
}

.item-img {
  display: flex;
  justify-content: space-around;
  height: 10rem;
}

@media only screen and (max-width: 820px) {
  .cart-item-container {
    width: auto;
    width: initial;
  }
}

@media only screen and (max-width: 684px) {
  .cart-item-container {
    width: 100%;
  }
}

#hero-img {
  margin-top: -2rem;
  z-index: -1;
  position: absolute;
  width: 100vw;
}

.heading {
  display: flex;
  flex-direction: column;
  margin: 9rem auto auto 9rem;
}

.heading h1 {
  font-family: "MuseoModerno", cursive;
  font-size: 3.5rem;
}

.heading h3 {
  font-family: "Play", sans-serif;
  font-size: 1.5rem;
  margin-top: 1rem;
}

@media only screen and (max-width: 767px) {
  .heading h1 {
    font-size: 2rem;
    width: 50%;
    flex-wrap: wrap;
  }
  .heading h3 {
    font-size: 1rem;
    width: 50%;
    flex-wrap: wrap;
  }

  .heading {
    margin: 3rem 3rem 5rem 3rem;
  }

  #hero-img {
    width: auto;
    height: 100vh;
  }
}

.shop-box {
  background: rgb(255, 241, 0);
  background: linear-gradient(
    305deg,
    rgba(255, 241, 0, 1) 24%,
    rgba(255, 220, 0, 1) 65%
  );
  margin-top: -2rem;
  padding-top: 2rem;
}

.shop-container {
  display: flex;
}

@media only screen and (max-width: 684px) {
  .shop-container {
    justify-content: center;
  }
}

@media only screen and (max-width: 330px) {
  .shop-container {
    justify-content: center;
    display: inline;
  }
}

.login-box {
  text-align: center;
  flex-direction: column;
  align-items: center;
  margin-top: -2rem;
  display: flex;
  justify-content: center;
  padding: 2rem;
  background: rgb(255, 241, 0);
  background: linear-gradient(
    305deg,
    rgba(255, 241, 0, 1) 24%,
    rgba(255, 220, 0, 1) 65%
  );
}

.login-container {
  margin: 3rem auto 5rem auto;
  padding: 4rem 13rem;
  background-color: white;
  border-radius: 1.3rem;
  display: flex;
  flex-direction: column;
  width: 40%;
  justify-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 3px 3px 20px rgb(34, 33, 33);
}

.login-heading {
  margin-top: 4rem;
  font-family: "Play", sans-serif;
  font-size: 2rem;
  font-weight: bold;
}

.login-container h5 {
  margin-top: 1rem;
}
.login-forgot-heading {
  width: 10rem;
}

.login-container a {
  font-size: 0.8rem;
}

@media only screen and (max-width: 767px) {
  .login-container {
    width: 70%;
  }
}

@media only screen and (max-width: 500px) {
  .login-container {
    width: 70%;
    padding: 3rem 6rem;
  }
}


.registration-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgb(255, 241, 0);
  background: linear-gradient(
    305deg,
    rgba(255, 241, 0, 1) 24%,
    rgba(255, 220, 0, 1) 65%
  );
  height: 100vh;
  font-family: "Play", sans-serif;
  font-size: 1rem;
  font-weight: bold;
  margin-top: -2rem;
  padding-top: 4rem;
}

.registration-container {
  display: flex;
  flex-direction: column;
  margin: 3rem auto 5rem auto;
  padding: 3rem 7rem;
  background-color: white;
  border-radius: 1.3rem;
  width: 40%;
  justify-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 3px 3px 20px rgb(34, 33, 33);
}

.input-field {
  display: flex;
  flex-direction: column;
}

@media only screen and (max-width: 767px) {
  .registration-container {
    margin: 3rem 1rem 5rem 1rem;
    width: 70%;
  }
}

@media only screen and (max-width: 330px) {
  .registration-form {
    height: 100%;
  }

  .registration-container {
    width: 90%;
  }
}


